

import XHeader from 'vux/src/components/x-header/index.vue'
import InlineCalendar from 'vux/src/components/inline-calendar/index.vue'

import MeScroll from 'mescroll.js'
import 'mescroll.js/mescroll.min.css'
export default {
name: 'vuxCalendar',
components: {XHeader,InlineCalendar},
props: [],
data() {
return {
mescroll: null,
newArr: [] 
}
},
mounted () {

this.mescroll = new MeScroll(this.$refs.mescroll, {// mountedmescroll,dom.
down: {
auto: false, // callback; true
callback: this.downCallback 
},
up: {
auto: true, // ; false
callback: this.upCallback, // ,;  callback: function (page) { upCallback(page); }
page: {
num: 0, // ,0,1,callback(page)1
size: 10 
},
noMoreSize: 5, // ,5;(),
toTop: { // 
src: './static/mescroll/mescroll-totop.png'
}
}
})
},
beforeRouteEnter (to, from, next) { // isBounce,beforeRouteEnter
next(vm => {
if (vm.mescroll) {

if (vm.mescroll.lastBounce != null) vm.mescroll.setBounce(vm.mescroll.lastBounce)

if (vm.mescroll.lastScrollTop) {
vm.mescroll.setScrollTop(vm.mescroll.lastScrollTop)
setTimeout(() => { // ,setScrollToponScroll,
vm.mescroll.setTopBtnFadeDuration(0)
}, 16)
}
}
})
},
beforeRouteLeave (to, from, next) { // isBounce,beforeRouteLeave
if (this.mescroll) {
this.mescroll.lastBounce = this.mescroll.optUp.isBounce
this.mescroll.setBounce(true) 
this.mescroll.lastScrollTop = this.mescroll.getScrollTop()
this.mescroll.hideTopBtn(0)
}
next()
},
methods: {
change(ev){
console.log(JSON.stringify(ev));

},
/*  */
downCallback () {
console.log('this.mescroll.version=' + this.mescroll.version);

this.getListDataFromNet(0, 1, (data) => {

this.newArr.unshift(data)

this.$nextTick(() => {
this.mescroll.endSuccess()
})
}, () => {

this.mescroll.endErr()
})
},


upCallback (page) {

this.getListDataFromNet(page.num, page.size, (curPageData) => {

this.newArr = this.newArr.concat(curPageData)

this.$nextTick(() => {
this.mescroll.endSuccess(curPageData.length)
})
}, () => {

this.mescroll.endErr()
})
},

/* 
,: http://www.mescroll.com/api.html#tagUpCallback
getListDataFromNet,,
,.
* */
getListDataFromNet (pageNum, pageSize, successCallback, errorCallback) {

setTimeout(function () {
try {

if (pageNum === 0) {

var id = new Date().getTime()
var newObj = {title: ' ' + id, content: '', id: id}
successCallback && successCallback(newObj)
} else {

var newArr = []
for (var i = 0; i < pageSize; i++) {
var upIndex = (pageNum - 1) * pageSize + i + 1
newArr.push({title: '' + upIndex + ' ', content: '', id: upIndex})
}
successCallback && successCallback(newArr)
}
} catch (e) {

errorCallback && errorCallback()
}
}, 1000)
}
}

}



